.spinner-container{
	display: none;
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: white;
	border: 5px solid white;
	z-index: 999;
	.spinner{
		position: absolute;
		width: 80px;
		height: 80px;
		left: calc(50% - 40px);
		top: calc(50% - 40px);
		transform: translate(-(calc(50% - 40px)), -(calc(50% - 40px)));
		animation: spinner-rotate 3s ease infinite;
		
		&-rect{
			position: absolute;

			&-top{
				height: 10px;
				width: 80%;
				left: 10%;
				top: 0;
				background: #FFCC00;
				animation: rect-top 2s ease infinite;
			}
			&-bottom{
				height: 10px;
				width: 80%;
				left: 10%;
				bottom: 0;
				background: #EA3810;
				animation: rect-bottom 2s ease infinite;
			}
			&-left{
				height: 80%;
				width: 10px;
				left: 0;
				top: 10%;
				background: #EA3810;
				animation: rect-left 2s ease infinite;
			}
			&-right{
				height: 80%;
				width: 10px;
				right: 0;
				top: 10%;
				background: #FFCC00;
				animation: rect-right 2s ease infinite;
			}
		}
	}
}

